import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import {Page} from "./../../models/page";

// declare var ENV: any;
@Injectable()
export class BaseApiService {

  public ENV;
  protected couchbaseHost: string;
  protected frHost: string;

  constructor(protected http: Http) {
    // this.ENV = ENV;
    // this.couchbaseHost = 'http://' + ENV.CB_HOST + ':' + ENV.CB_PORT + '/db/';
      this.couchbaseHost = 'http://52.53.228.249:4984/db/';
    this.frHost = '/';
  }

  public frPost(url: string, body: any): Observable<any> {
      return this.http.post(this.frHost + url, body)
          .catch(this.handleError);
  }
  public frGet(url: string, headers = null, mapFxn = null):  Observable<any> {
      return this.http.get(this.frHost + url)
          .map(mapFxn || this.extractData)
          .catch(this.handleError);
  }
  public get(url: string, headers = null, mapFxn = null):  Observable<any> {
    return this.http.get(this.couchbaseHost + url)
      .map(mapFxn || this.extractData)
      .catch(this.handleError);
  }

  public del(url: string): Observable<any> {
    return this.http.delete(this.couchbaseHost + url)
      .catch(this.handleError);
  }

  public put(url: string, body: any): Observable<any> {
    return this.http.put(this.couchbaseHost + url, body)
      .catch(this.handleError);
  }

  public post(url: string, body: any): Observable<any> {
    return this.http.post(this.couchbaseHost + url, body)
      .catch(this.handleError);
  }

  public extractData(res: Response) {
    let body;
    try {
      body = res.json();
      return body || {};
    } catch (e) {
      console.error('Cannot extract Json from response body: ', res);
    }
  }

  private handleError(error: any) {
    // In a real world app, we might use a remote logging infrastructure
    // We'd also dig deeper into the error to get a better message
    console.error('error', error);
    let errMsg = (error.message) ? error.message :
      error.status ? `${error.status} - ${error.statusText}` : 'Server error';
    console.error(errMsg); // log to console instead
    return Observable.throw(errMsg);
  }

}



