import { Observable } from 'rxjs/Rx';
import { ConfirmDialog } from './confirm-dialog.component';
import { AlertDialogComponent } from './alert-dialog.component';
import { MdDialogRef, MdDialog} from '@angular/material';
import { Injectable } from '@angular/core';
import {ImageDialogComponent} from './image-dialog.component';

@Injectable()
export class DialogService {

  constructor(private dialog: MdDialog) { }

  public confirm(title: string, message: string): Observable<boolean> {

    let dialogRef: MdDialogRef<ConfirmDialog>;

    dialogRef = this.dialog.open(ConfirmDialog);

    dialogRef.componentInstance.title = title;
    dialogRef.componentInstance.message = message;

    return dialogRef.afterClosed();
  }

  public alert(title: string, message: string) {

        let dialogRef: MdDialogRef<AlertDialogComponent>;

        dialogRef = this.dialog.open(AlertDialogComponent);

        dialogRef.componentInstance.title = title;
        dialogRef.componentInstance.message = message;

        return dialogRef.afterClosed();
  }

  public showImage(src: string, x: any, y: any, w: any, h: any) {
      let dialogRef: MdDialogRef<ImageDialogComponent>;

      dialogRef = this.dialog.open(ImageDialogComponent);

      dialogRef.componentInstance.src = src;
      dialogRef.componentInstance.x = x;
      dialogRef.componentInstance.y = y;
      dialogRef.componentInstance.h = h;
      dialogRef.componentInstance.w = w;

      return dialogRef.afterClosed();
  }
}



